n-button {\n  @include flex_center;\n  margin-top: 1rem;\n\n  button {\n    @include set_main_button_properties_given_height(1.5rem);\n    width: 9rem;\n    color: white;\n    font-weight: bold;\n    letter-spacing: 0.02rem;\n    background-color: #1db954;\n  }\n\n  @media (any-hover: hover) {\n    button:hover {\n      background-color: #1ed760;\n    }\n  }\n\n  button:active {\n    background-color: #1aa34a;\n  }\n}\n\n%checkbox-item {\n  // @include flex_justify_content;\n  white-space: pre;\n  $font-size: 0.8rem;\n  font-size: $font-size;\n  padding: $font-size/1.5;\n\n  input[type=\"checkbox\"] {\n    height: $font-size;\n    width: $font-size;\n  }\n\n  input:checked[type=\"checkbox\"] + span {\n    background-color: lightblue;\n  }\n}\n\n.region-item {\n  @extend %checkbox-item;\n\n  svg {\n    margin: auto;\n    height: 0.6rem;\n  }\n\n  td {\n    text-align: center;\n    background-color: floralwhite;\n  }\n\n  td:nth-child(1) {\n    text-align: left;\n    position: sticky;\n    left: 0;\n  }\n}\n\n.select-all-item {\n  @extend %checkbox-item;\n  margin-top: 0.5rem;\n  margin-bottom: -0.75rem;\n}\n\n.region-list-header {\n  font-size: 0.75rem;\n\n  th {\n    padding: 0 1rem;\n    font-weight: normal;\n    font-size: 0.5rem;\n    position: sticky;\n    top: 0;\n    background-color: floralwhite;\n    z-index: 1;\n  }\n\n  th:nth-child(1) {\n    left: 0;\n    z-index: 2;\n  }\n}\n\n.region-list {\n  margin: 1rem 1rem;\n  margin-bottom: 0;\n  overflow: scroll;\n\n  table {\n    border-spacing: 0;\n    border-collapse: collapse;\n  }\n}\n\n.description {\n  @include flex_center;\n  margin: 0.5rem 3rem 0;\n\n  span {\n    font-size: 0.75rem;\n    text-align: center;\n    white-space: pre-wrap;\n\n    a {\n      text-decoration: none;\n    }\n  }\n}\n\n.output {\n  display: flex;\n  margin: 0.5rem auto auto;\n  width: 15rem;\n  background-color: white;\n\n  p {\n    line-height: 0.6rem;\n    margin-block: 0;\n  }\n\n  span {\n    white-space: pre-wrap;\n    font-